*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.Result-Content{
  background-color: rgba(255, 255, 255);
}

.Result-Content h1{
  margin: 2vh 0 1.5vh 3%;
  display: flex;
  justify-content: center;
}

.Result-Content img{
  width: 90%;
  height: 60vh;
  margin-left: 5%;
  border-radius: 2%;
  box-shadow: 3px 3px 2px rgb(115, 115, 115);
}

.Middle-Part{
  display: grid;
  grid-template-columns: 50% 50%;
}
.Details h4{
  border-bottom: 2px black solid;
  padding: 1%;
  margin-right: 4%;
}
.Details h4 span{
  margin-right: 2%;
}

.Result-Content p{
  margin: 2vh 10% 0 3%;
  font-size: larger;
  font-weight: bold;
}

.Xmark{
  width: 2%;
  height: 1.8%;
  font-size: larger;
  background-color: white;
  border: rgb(35, 35, 35) 1px solid;
  border-radius: 50%;
  position: relative;
  left: 49%;
  top: 1vh;
  cursor: pointer;
}

.Xmark:hover{
  font-weight: bolder;
  box-shadow: 2px 2px 2px gray;
}