@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
.Navbar-Content{
  position: sticky;
  top: 0;
  z-index: 4;
}
.Navbar{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 15vh;
  background-color: rgba(255, 255, 255);
  box-shadow: 2px 2px 3px rgb(66, 79, 79);
}

.Logo-Image{
  width: 7%;
  height: 10vh;
}

.Social-Info a{
font-size: 4vh;
color: black;
padding: 10px 20px;
}

.Social-Info a:hover{
  box-shadow: 0.5px 0.5px 2px gray;
  text-shadow: 0.5px 0.5px 2px gray;
  }

.Navbar a{
  text-decoration: none;
}
.Navbar h2{
  font-size: 4vh;
  color: black;
}

.Navbar h2:hover + span{
  display: block;
  background-color: red;
  width: auto;
  height: 2px;
  transition: 0.6s ease-in-out;
}

.Main-Body-Content1{
  display: flex;
  justify-content: center;
  margin-top: 3vh;
  font-size: 6vh;
  margin-bottom: 3vh;
}
.FirstH::after{
  content: "";
  animation: myfirst 2s 0s infinite backwards ease-out;
}
.SecondH::after{
  content: "";
  animation: myfirst 2s 0s infinite forwards ease-out;
}
@keyframes myfirst {
  0%{
    content: ".";
  }
  50%{
    content: ". .";
  }
  100%{
    content: ". . .";
  }
}
.Main-Body-Image{
  background-image: url(./Images/Anime-mix.webp);
  width: 100%;
  height: 73vh;
  background-repeat: no-repeat;
  background-size: 80% 70vh;
  background-position-x: 50%;
  box-shadow: 2px 2px 3px rgb(66, 79, 79);
}

.Blog-Body h2{
  display: flex;
  justify-content: center;
  margin-top: 3vh;
  font-size: 8vh;
}

.Blog-Body-Content{
  display: grid;
  grid-template-columns: repeat(3, 30%);
  margin-left: 5%;
}

.Blog-Body-Content div{
  margin: 3vh 3% 3vh 2.5%;
  background-color: rgba(159, 187, 178, 0.185);
  box-shadow: 3px 3px 3px rgba(159, 187, 178) ;
  border-radius: 3%;
}
.Blog-Body-Content div h3{
  margin: 2vh 0 1vh 5%;
  font-size: 3.5vh;
}
.Blog-Body-Content div img{
  margin-left:10%;
  width: 70%;
  max-height: 65%;
  box-shadow: 3px 3px 3px rgba(159, 187, 178) ;
  border-radius: 3%;
}

.Details1{
  display: block;
  margin-left:10%;
  width: 22%;
  font-size: 3vh;
  box-shadow: 2px 2px 2px rgba(159, 187, 178) ;
  border-radius: 3%;
  cursor: pointer;
  font-weight: bolder;
  margin-top: 0.5vh;
  color: black;
  text-decoration: none;
}

.icon1, .icon2, .icon3, .icon4{
  animation: float 5s ease-in-out infinite;
}

.icon2{
  animation-delay: 1s;
}
.icon3{
  animation-delay: 2s;
}
.icon4{
  animation-delay: 3s;
}

@keyframes float {
  0%{
    transform: translateY(0);
  }
  50%{
    transform: translateY(-15px);
  }
  100%{
    transform: translateY(0);
  }
}


